import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Projects from '../views/Projects.vue'
import Error from '../views/Error.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      title: 'Home | Safiyyah Thur Rahman'
    }
  }, {
    path: '/projects',
    name: 'Projects',
    component: Projects,
    meta: {
      title: 'Projects | Safiyyah Thur Rahman'
    }
  },
  {
    path: '/404',
    component: Error,
    meta: {
      title: 'Error | Safiyyah Thur Rahman'
    }
  },
  {
    path:'*',
    redirect:'/404'
  }

]

const router = new VueRouter({
  routes,
  mode:"history"
})

export default router
