<template>
  <div id="app m-0">
    <NavBar />
    <router-view />
    <ScrollToTop />
    <Footer />
  </div>
</template>

<script>
import Footer from "./components/Footer";
import NavBar from "./components/NavBar";
import ScrollToTop from "./components/ScrollToTop";
export default {
  name: "App",
  components: {
    Footer,
    NavBar,
    ScrollToTop,
  },
  created() {
    window.addEventListener("scroll", this.changeNavBarBg);
  },
  mounted(){
    let path = localStorage.getItem('path');
    if(path) {
      localStorage.removeItem('path');
      this.$router.push(path);
    }
  },
  methods: {
    changeNavBarBg() {
      var height = document.getElementById("header-custom").clientHeight;
      height = height - 50;
      if (
        document.body.scrollTop > height ||
        document.documentElement.scrollTop > height
      ) {
        document.getElementById("navbar-custom").style.backgroundColor =
          "#e90b5a";
        document.getElementById("scroll-to-top-custom").style.display = "block";
      } else {
        document.getElementById("navbar-custom").style.backgroundColor =
          "transparent";
        document.getElementById("scroll-to-top-custom").style.display = "none";
      }
    },
  },
};
</script>

<style lang="scss">
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans&display=swap');
div {
  font-family: 'Noto Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #009e93;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #067c74;
}

/* Animation */

@keyframes fadeInUp {
  from {
    transform: translate3d(0, 40px, 0);
  }

  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

@-webkit-keyframes fadeInUp {
  from {
    transform: translate3d(0, 40px, 0);
  }

  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
  -webkit-animation-duration: 1s;
  -webkit-animation-fill-mode: both;
}

.animatedFadeInUp {
  opacity: 0;
}

.fadeInUp {
  opacity: 0;
  animation-name: fadeInUp;
  -webkit-animation-name: fadeInUp;
}
</style>
